@charset "UTF-8";
.swiper_stepper {
  overflow: hidden;
  background: gray;
  margin: 50px;
}
.swiper_stepper .dosirak_area {
  margin: 0 auto;
  width: 80%;
  height: 200px;
  background: gray;
}
.swiper_stepper .dosirak_area .dosirak {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  width: 100%;
  height: 100%;
}
.swiper_stepper .dosirak_area .dosirak li {
  background: purple;
  border: 1px solid gray;
  font-size: 20px;
}
.swiper_stepper .stepper_swiper {
  overflow: visible;
  margin-top: 100px;
}
.swiper_stepper .stepper_swiper .swiper-slide ul {
  display: grid;
  grid-template-columns: 1fr 1fr;
}
.swiper_stepper .stepper_swiper .swiper-slide ul li {
  margin: 10px;
}
.swiper_stepper .stepper_swiper .swiper-slide ul li label div {
  font-size: 20px;
  padding: 50px 0;
  background: yellowgreen;
}
.swiper_stepper .stepper_swiper .swiper-slide ul li input {
  display: none;
}
.swiper_stepper .stepper_swiper .swiper-slide ul li input:checked + div {
  background: yellow;
}
.swiper_stepper .stepper_swiper .swiper-button-next,
.swiper_stepper .stepper_swiper .swiper-button-prev {
  top: -30%;
  color: yellow;
}
.swiper_stepper .stepper_swiper .swiper-button-next {
  pointer-events: none;
  opacity: 0.5;
}
.swiper_stepper .stepper_swiper .swiper-button-next.on {
  pointer-events: unset;
  opacity: 1;
}
.swiper_stepper .stepper_swiper .swiper-pagination {
  bottom: 110%;
}
.swiper_stepper .stepper_swiper .swiper-pagination-bullet {
  background-color: yellow;
  width: 30px;
  height: 30px;
}
.swiper_stepper .stepper_swiper .swiper-pagination-bullet.active {
  opacity: unset;
  background-color: yellow; /* active된 bullet의 배경 색상 */
}
.swiper_stepper .stepper_swiper .swiper-button-disabled {
  pointer-events: none !important;
  opacity: 0 !important;
}